* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "微软雅黑", 'sans-serif';
  -webkit-tap-highlight-color: transparent;
}
html {
  min-height: 100%;
  max-width: 640px;
  margin: 0 auto;
}
html body {
    max-width: 640px;
    margin: 0 auto;
    font-size: 14px;
    height: 100%;
    font-size: 14px;
}
html body button,
    html body input {
      -webkit-appearance: none;
      outline: none;
}
html body ol,
    html body ul {
      list-style: none;
}
html body a,
    html body a:hover,
    html body a:focus {
      text-decoration: none;
      color: #333;
      outline: none;
}
html body em,
    html body i {
      font-style: normal;
}
html body header.mint-header {
      background: #ce3d3a;
      font-size: 18px;
}
html body .bgwhite {
      background: #fff;
}
html body .mt-progress-progress {
      background: #ce3d3a;
}
html body .mint-header .frequency {
      width: 26px;
      height: 26px;
      margin-top: 2px;
}
html body .imgbox img {
      width: 100%;
      height: 100%;
}
.coverbg {
  background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
}
.coverbg.bg1 {
    background-color: #DCD9D4;
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), radial-gradient(at 50% 0%, rgba(255, 255, 255, 0.1) 0%, rgba(0, 0, 0, 0.5) 50%);
    background-blend-mode: soft-light, screen;
}
.coverbg.bg2 {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.15) 0%, rgba(0, 0, 0, 0.15) 100%), radial-gradient(at top center, rgba(255, 255, 255, 0.4) 0%, rgba(0, 0, 0, 0.4) 120%) #989898;
    background-blend-mode: multiply, multiply;
}
.coverbg.bg3 {
    background-image: linear-gradient(to top, #6a85b6 0%, #bac8e0 100%);
}
.coverbg.bg4 {
    background-image: linear-gradient(to left, #BDBBBE 0%, #9D9EA3 100%), radial-gradient(88% 271%, rgba(255, 255, 255, 0.25) 0%, rgba(254, 254, 254, 0.25) 1%, rgba(0, 0, 0, 0.25) 100%), radial-gradient(50% 100%, rgba(255, 255, 255, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%);
    background-blend-mode: normal, lighten, soft-light;
}
.coverbg.bg5 {
    background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
}
.coverbg .mint-header {
    background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
    max-width: 640px;
    width: 100%;
    right: 0;
    margin: 0 auto;
}
.coverbg .mint-header .mint-header-button .mint-button--default .mintui-back {
      color: #fff !important;
}
.coverbg .songlistinfo {
    padding-top: 40px;
}
.coverbg .songlistinfo .search {
      height: 30px;
      background: #a10;
}
.coverbg .songlistinfo .middleinfo {
      padding: 10px;
      font-size: 0;
}
.coverbg .songlistinfo .middleinfo .imgbox {
        display: inline-block;
        width: 144px;
        height: 144px;
}
.coverbg .songlistinfo .middleinfo .imgbox img {
          height: 100%;
          width: 100%;
}
.coverbg .songlistinfo .middleinfo .info {
        display: inline-block;
        height: 144px;
        width: calc(100% - 144px);
        font-size: 14px;
        vertical-align: top;
        padding: 10px;
}
.coverbg .songlistinfo .middleinfo .info .listname {
          margin: 10px 0 20px 0;
          font-size: 15px;
          color: #fff;
          min-height: 20px;
}
.coverbg .songlistinfo .middleinfo .info .owner {
          font-size: 13px;
          color: #fff;
}
.coverbg .songlistinfo .middleinfo .info .owner img {
            width: 30px;
            height: 30px;
            border-radius: 50%;
            vertical-align: middle;
}
.coverbg .songlistinfo .buttonarea {
      height: 50px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.coverbg .songlistinfo .buttonarea a {
        -webkit-box-flex: 1;
        -ms-flex: 1;
        flex: 1;
        text-align: center;
        font-size: 12px;
        color: #fff;
}
.coverbg .songlistinfo .buttonarea a i {
          display: block;
          font-size: 22px;
          color: #fff;
          margin-bottom: 3px;
          margin-top: 2px;
}
.coverbg .slide-enter-active,
  .coverbg .slide-leave-active {
    transition: opacity 0.5s;
}
.coverbg .slide-enter,
  .coverbg .slide-leave-active {
    opacity: 0;
}
.coverbg .songlist {
    background: #fff;
    padding-bottom: 50px;
}
.coverbg .songlist.stickUp {
      padding-top: 40px;
}
.coverbg .songlist.stickUp .playAll {
        position: fixed;
        top: 40px;
        left: 0;
        width: 100%;
        z-index: 10;
}
.coverbg .songlist .playAll {
      height: 50px;
      line-height: 50px;
      padding: 0 10px;
      background: #fff;
      font-size: 17px;
}
.coverbg .songlist .playAll i {
        vertical-align: middle;
        color: #333;
}
.coverbg .songlist .playAll em {
        font-size: 13px;
        color: #666;
}
.coverbg .songlist .playAll .playIcon {
        font-size: 22px;
}
.coverbg .songlist .playAll span {
        float: right;
        color: #666;
        font-size: 14px;
}
.coverbg .songlist .playAll span i {
          font-size: 18px;
          margin-right: 5px;
}
.coverbg .songlist .mint-cell {
      overflow: visible;
}
.coverbg .songlistItemPopup {
    width: 100%;
    background: #fff;
}
.coverbg .songlistInfoPopup {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
}



